import { getTokenLocal } from "../utils/localStorage.util";
import ApiRoutes from "../configs/endpoints.config";
import HttpClient from "./index.api";
const baseURL = process.env.REACT_APP_API_URL;

class Booth extends HttpClient {
  constructor() {
    super(baseURL);
    this._initializeRequestInterceptor();
    this._initializeResponseInterceptor();
  }

  _initializeRequestInterceptor = () => {
    this.instance.interceptors.request.use((config) => {
      config.headers["Authorization"] = `Bearer ${getTokenLocal()}`;
      return config;
    });
  };

  _initializeResponseInterceptor = () => {
    this.instance.interceptors.response.use(
      (response) => {
        return response;
      },
      (response) => {
        return Promise.resolve(response);
      }
    );
  };

  AllBoothConfig = ApiRoutes.Booth.All;
  BoothByIdConfig = ApiRoutes.Booth.BoothById;
  AddBoothConfig = ApiRoutes.Booth.AddBooth;
  EditBoothConfig = ApiRoutes.Booth.EditBooth;
  DeleteBoothConfig = ApiRoutes.Booth.DeleteBooth;

  getAllBooth = async () => {
    return this.instance({
      method: this.AllBoothConfig.Method,
      url: this.AllBoothConfig.Endpoint,
      headers: {},
      data: null,
    });
  };
  getBoothById = async (data) => {
    return this.instance({
      method: this.BoothByIdConfig.Method,
      url: this.BoothByIdConfig.Endpoint,
      headers: {},
      data: data,
    });
  };
  addBooth = async (data) => {
    return this.instance({
      method: this.AddBoothConfig.Method,
      url: this.AddBoothConfig.Endpoint,
      headers: {},
      data: data,
    });
  };
  editBooth = async (data) => {
    return this.instance({
      method: this.EditBoothConfig.Method,
      url: this.EditBoothConfig.Endpoint,
      headers: {},
      data: data,
    });
  };
  deleteBooth = async (data) => {
    return this.instance({
      method: this.DeleteBoothConfig.Method,
      url: this.DeleteBoothConfig.Endpoint,
      headers: {},
      data: data,
    });
  };
}

export default Booth;
